.headName {
    font-family: 'Rajdhani', sans-serif;
    font-size: 10vw;
    text-align: center;
    width: 50%;
    margin: auto;
}

.headName p {
    overflow: hidden; 
    border-right: .10em solid rgb(0, 255, 242);
    white-space: nowrap; 
    margin: 0 auto; 
    letter-spacing: .15em;
    -webkit-animation: 
      typing 2.5s steps(40, end),
      blink-caret .75s step-end infinite;
            animation: 
      typing 2.5s steps(40, end),
      blink-caret .75s step-end infinite;
  }
  
  /* The typing effect */
  @-webkit-keyframes typing {
    from { width: 0 }
    to { width: 100% }
  }
  @keyframes typing {
    from { width: 0 }
    to { width: 100% }
  }
  
  /* The typewriter cursor effect */
  @-webkit-keyframes blink-caret {
    from, to { border-color: transparent }
    50% { border-color: lightgreen; }
  }
  @keyframes blink-caret {
    from, to { border-color: transparent }
    50% { border-color: lightgreen; }
  }


